import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			payType: 1,
			cardList: [],
			card_id: 0,
			totalFee: 0,
			choose_number:1
		};
	},
	computed: {

	},
	onLoad(options) {
		let _this = this;
		_this.getPageData(options);
	},

	methods: {
		chooseNumber(value){
			this.choose_number = value
			this.calculateTotol()
		},
		getCardId(index){
			let _this = this; 
			_this.card_id = index;
			_this.calculateTotol()
		},
		calculateTotol(){
			let key = this.card_id;
			let card = this.cardList[key];
			if(this.cardList.length > 0){
				this.totalFee = (card.price*1*this.choose_number).toFixed(2);
			}
		},
		checkPay(id) {
			var _this = this;
			let data = {
				'id': id
			};
			this.$http.post('/api/card/checkPay', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					if (data.paySuccess == true) {
						clearInterval(_this.timeId)
						uni.showToast({
							title: '充值成功！',
							icon: 'success'
						});
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/coupons/get'
							})
						}, 1000)
					}
				}
			}).catch(err => {
				console.log(err);
			})
		},
		getPageData(data) {
			var _this = this;
			uni.showLoading({
				title:'加载中...',
			})
			this.$http.post('/api/card/getCardList', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					_this.cardList = data;
					this.calculateTotol()
					uni.hideLoading()
				}
			}).catch(err => {
				console.log(err);
			})
			uni.hideLoading()
		},
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm: function() {
			var _this = this;
			if (_this.payType != 1) {
				uni.showToast({
					title: '暂不支持该种支付方式！',
					icon: 'none'
				});
				return false;
			}
			let card = _this.cardList[_this.card_id];
			if (_this.cardList.length == 0) {
				uni.showToast({
					title: '暂无可选择储值卡！',
					icon: 'none'
				});
				return false;
			}
			let data = {
				'card_id': card.id,
				'number': _this.choose_number
			}
			this.$http.post('/api/card/doPay', data).then(res => {
				if (res.data.code != 1 && res.data.code != 19) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				}else{
					var data = res.data.data
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: data.timeStamp,
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign,
						success: function(res) {
							_this.timeId = setInterval(function() {
								_this.checkPay(data.orderId);
							}, 500)
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			}).catch(err => {
				console.log(err);
			})

		},
	}
}
